import "../../vendor/mathquill/mathquill.js"

import * as React from 'react';
import {render} from 'react-dom';
import {Editor} from './editor';

export class FormulaEditor {
    editor: any;
    constructor(selector: any, submitFn?: (latex: string, svgHTML: string)=> void, cancelFn?: ()=> void) {
        this.editor = render(<Editor submit={(latex, svgHTML)=> {
                submitFn && submitFn(latex, svgHTML);
            }} cancel={()=> {
                cancelFn && cancelFn();
            }}/>, document.querySelector(selector));
    }
}