import * as React from 'react';


export class LatexInput extends React.Component<{}, {}> {
    className: any;
    refs: {
        body: HTMLElement,
        input: HTMLInputElement
    };
    props: {
        change: (latex: string)=> void
    };
    componentDidMount() {
        this.className = this.refs.body.className;
    }
    change() {
        this.props.change && this.props.change(this.refs.input.value);
    }
    latex(latex: string = null) {
        latex!=null && this.refs.input.value!=latex && (this.refs.input.value = latex);
        return this.refs.input.value;
    }
    write(latex: string) {
        latex && ((this.refs.input.value += latex) && this.change());
    }
    hide(hide: boolean) {
        this.refs.body.className = hide?this.className+" hide":this.className;
        !hide && this.refs.input.focus();
    }
    render() {
        return (
            <div className="fe-latex-input" ref="body">
                <textarea id="latexInput" ref="input" onKeyUp={
                    this.change.bind(this)
                } spellCheck={false} />
            </div>
        );
    }
}