import * as React from 'react'
declare let MathJax: any;
import debounce = require('lodash/debounce');
export class LatexPreview extends React.Component<{}, { latexError: boolean }> {
    constructor(props: any) {
        super(props);
        this.set = debounce(this.set.bind(this), 300); //
        this.state = { latexError: false };
    }
    public refs: {
        container: HTMLElement;
        codeCon: HTMLElement;
    };
    componentDidMount() {
        MathJax.Hub.Config({
            jax: ["output/SVG"],
            SVG: {
                scale: 120,
                linebreaks: { automatic: true },
                useGlobalCache: false
            },
            messageStyle: "none",
            showProcessingMessages: false,
            tex2jax: { inlineMath: [["$", "$"], ["\\(", "\\)"]] }
        });
    }
    set(latex: string, callback: (svg: any) => void) {

        this.refs.codeCon.innerText = '$$' + latex + '$$';
        this.refs.container.style.display = 'none';
        MathJax.Hub.Queue(["Typeset", MathJax.Hub, (params: any) => {
            const innerHTML = this.refs.codeCon.innerHTML;
            if (innerHTML.indexOf('$$') !== 0 && this.refs.container.innerHTML !== innerHTML) {
                this.refs.container.innerHTML = innerHTML;
                this.refs.container.style.display = 'block';
                callback(this.refs.container.getElementsByTagName("svg")[0]);
                if (this.refs.container.innerHTML.indexOf('mathcolor="red"') !== -1) {
                    this.setState({
                        latexError:true
                    });
                } else {
                    this.setState({
                        latexError:false
                    });
                }
            }
        }]);
    }
    get svg() {
        return this.refs.container.getElementsByTagName("svg")[0];
    }
    render() {
        return (
            <div className="fe-latex-preview">
                <span className="latex-preview-text">:</span>
                <div style={{ display: 'none' }} ref="codeCon"></div>
                <div className="latex-preview-content" ref="container"></div>
                <span className="latex-error"
                    style={this.state.latexError ? { display: "block" } : { display: "none" }}>
                    LaTex
                </span>
            </div>
        )
    }
}