import * as React from "react"
import { Toolbar } from "./toolbar"
import { Visual } from "./visual"
import { LatexInput } from "./latex-input"
import { LatexPreview } from "./latex-preview"
import { Switch } from "./switch"

enum EditorMode {
    Visual,
    Input
}

interface EditorProps {
    submit: (latex: string, svg: string) => void;
    cancel: () => void;
}

export class Editor extends React.Component<EditorProps, {}>{
    refs: {
        visual: Visual,
        preview: LatexPreview,
        input: LatexInput,
        switch: Switch
    };
    _mode: EditorMode = EditorMode.Visual;
    latex: string = '';
    state = {
        loading: false,
        updating: false
    }
    componentDidMount() {
        this.refs.input.hide(true);
    }
    change(latex: string) {
        switch (this.mode) {
            case EditorMode.Input:
                this.refs.visual.latex(latex);
                this.refs.input.refs.input.focus();
                break;
            case EditorMode.Visual:
                this.refs.input.latex(latex);
                this.refs.visual.mq.focus();
                break;
        }

        this.setState({ loading: true });

        this.refs.preview.set(latex, () => {
            this.setState({ loading: false });
        });
        this.latex = latex;

    }
    set mode(mode) {
        switch (mode) {
            case EditorMode.Input:
                this.refs.visual.hide(true);
                this.refs.input.hide(false);
                this.refs.switch.checked(true);
                break;
            case EditorMode.Visual:
                this.refs.visual.hide(false);
                this.refs.input.hide(true);
                this.refs.switch.checked(false);
                break;
        }
        this._mode = mode;
    }
    get mode() {
        return this._mode;
    }
    submit() {
        //console.log(this.latex);
        // console.log(this.refs.preview.svg == undefined);
        if(this.refs.preview.svg == undefined){
            var svgHTML = '<svg></svg>';
        }else{
            var svgHTML = this.refs.preview.svg.outerHTML.replace(/xlink:href="http[0-9a-z:\/.?#=&;P]+#/ig, 'xlink:href="#');
        }
        this.props.submit(this.latex, svgHTML);
    }
    cancel() {
        this.props.cancel();
    }
    focus() {
        this.refs.visual.mq.focus();
    }
    set(latex: string, mode: EditorMode) {
        this.latex = latex;
        this.mode = mode;
        this.refs.visual.latex(latex);
        this.refs.input.latex(latex);
    }
    render() {
        return (
            <div className="fe-container">
                <Toolbar latex={(latex: string) => {
                    this.mode == EditorMode.Visual ? this.refs.visual.write(latex) : this.refs.input.write(latex)
                }} />
                <Visual ref="visual" change={(latex: string) => {
                    this.change(latex);
                }} />
                <LatexInput ref="input" change={(latex) => {
                    this.change(latex);
                }} />
                <LatexPreview ref="preview" />
                {
                    this.mode === EditorMode.Input ?
                        <TextTip />
                        :
                        <div className="fe-tip fe-tip-img"></div>
                }
                <div className="fe-footer">
                    <span className="switch-title">LaTeX</span>
                    <Switch ref="switch" click={(checked) => {
                        this.mode = checked ? EditorMode.Input : EditorMode.Visual;
                        this.setState({});
                    }} />
                    <button className={`btn  ${this.latex == '' ? 'btn-submit-disabled' :'btn-submit'}`}  onClick={this.submit.bind(this)} disabled={this.state.loading}>{
                        this.state.updating ? "" : ""
                    }</button>
                    {/* {<button className="btn btn-cancel" onClick={this.cancel.bind(this)}></button>} */}
                </div>
            </div>
        );
    }
}



interface ITextTipProps { };

interface ITextTipState { 
    close:boolean
};

class TextTip extends React.Component<ITextTipProps, ITextTipState> {
    constructor(props: any) {
        super(props);
        this.state = { close:false };
    }
    public render(): JSX.Element {
        return (
            <div className="fe-tip fe-tip-text"
                style={this.state.close ? {display:'none'}:{}}>
                <button className="icons icon-clear" onClick={() => {
                    this.setState({
                        close:true
                    });
                }}>
                </button>
                <p>:</p>
                <p>LaTeXLaTeX</p>
            </div>
        );
    }
}
