import * as React from "react";
declare let MathQuill: {
    new(): Object;
    getInterface(code: number): any;
};

interface VisualProps {
    change: (latex: string)=>void;
}

export class Visual extends React.Component<VisualProps, {}> {
    mq: any;
    className: any;
    refs: {
        body: HTMLElement
    };
    componentDidMount() {
        this.mq = MathQuill.getInterface(2).MathField(this.refs.body, {
            spaceBehavesLikeTab: true,
            handlers: {
                edit: ()=> {
                    this.props.change && this.props.change(this.mq.latex());
                }
            }
        });
        this.className = this.refs.body.className;
    }
    latex(latex:string=null) {
        latex!=null && (latex!=this.mq.latex()) && this.mq.latex(latex);
        return this.mq.latex();
    }
    write(latex: string) {
        this.mq.write(latex);
    }
    hide(hide: boolean) {
        this.refs.body.className = hide?this.className+" hide":this.className;
        this.mq.focus();
    }
    render() {
        return <div className="fe-visual" ref="body"></div>
    }
}