import * as React from 'react';
import {Data} from "./data";
import ComponentElement = React.ComponentElement;
import ReactNode = React.ReactNode;
import EventHandler = React.EventHandler;

interface ToolbarProps {
    latex: (latex: string)=> void
}

export class Toolbar extends React.Component<ToolbarProps, {}> {
    state = {active: -1};
    componentDidMount() {
        window.addEventListener('click', ()=> {
            this.setState({active: -1});
        }, false);
    }
    display(index: number) {
        this.setState({active: index});
    }
    insert(latex: string) {
        this.setState({active: -1});
        this.props.latex && this.props.latex(latex);
    }
    render() {
        return <div className="fe-toolbar">
            {
                Data.toolbar.map((item, i)=> {
                    return React.createElement(Symbol, {
                        image: item.img,
                        key: "symbol_"+i,
                        name: item.className,
                        active: this.state.active == i,
                        content: item.symbol.map((symbol, j)=>{
                            return React.createElement(SymbolItem, {
                                key: "symbol_item_"+j,
                                symbol: symbol,
                                click: ()=> {
                                    this.insert(symbol.latex)
                                }
                            });
                        }),
                        click: (event: any)=> {
                            event.stopPropagation();
                            this.display(i)
                        }
                    })
                })
            }
        </div>
    }
}

interface SymbolProps {
    click: (event: any)=> void;
    active: boolean;
    image: string;
    name: string;
    content: Array<ComponentElement<{children?: Array<ReactNode> & SymbolItemProps}, SymbolItem>>;
}

class Symbol extends React.Component<SymbolProps, {}> {

    get className() {
        return this.props.active?"fe-symbol active":"fe-symbol";
    }
    render() {
        return (
            <div className={this.className} onClick={(event)=> {
                this.props.click(event);
            }}>
                <span className={"image image-"+this.props.image}></span><br/>{this.props.name}
                <div className="fe-symbol-layer" style={this.props.content.length > 4 && (this.props.name != "" && this.props.name != "" && this.props.name != "")? { width: "200px",left:'-52px' } : {left:'-20px'}} >
                    <div className="fe-symbol-layer-triangle" style={this.props.content.length > 4 && (this.props.name != "" && this.props.name != "" && this.props.name != "") ? { } : { left: '51px' }}>

                    </div>
                    {this.props.content}

                </div>
            </div>
        )
    }

}
interface SymbolItemProps {
    click: (latex: string)=> void,
    symbol: any
}
class SymbolItem extends React.Component <SymbolItemProps, {}>{
    render() {
        let click = (event: any)=> {
            event.stopPropagation();
            this.props.click(this.props.symbol.latex)
        };
        return <span className={"image image-"+this.props.symbol.img} onClick={click}/>
    }
}