import * as React from 'react';

interface SwitchProps {
    click: (checked: boolean)=> void;
}

export class Switch extends React.Component<SwitchProps, {}> {
    refs: {
        checkbox: HTMLInputElement
    };
    click() {
        this.props.click(this.refs.checkbox.checked);
        invokeMethod('LaTeX',this.refs.checkbox.checked);
    }
    checked(checked?: boolean) {
        this.refs.checkbox.checked = checked;
    }
    render() {
        return (
            <label className="switch-btn switch-light switch-ios">
                <input type="checkbox" onClick={this.click.bind(this)} ref="checkbox"/>
                <span><span> </span><span> </span><a> </a></span>
            </label>
        )
    }

}